(function() {
  app.behaviors.utils = {
    _locateParentNode: function(el, nodeName) {
      while (el.nodeName !== nodeName) {
        el = el.parentNode;
      }
      return el;
    },
    _locateRepeater: function(el) {
      while (el.nodeName !== 'TEMPLATE') {
        console.log(el.nodeName);
        if (el.parentNode) {
          el = el.parentNode;
        } else if (el.domHost) {
          el = el.domHost;
        } else {
          console.log('badluck');
          break;
        }
      }
      return el;
    }
  };

}).call(this);
